<template>
<div>
    <div class="jumbotron text-center bg-dark text-info">
        <h1 class="display-4">TODO LIST App</h1>
        <p class="lead">Crud aplication with Firebase and Vue.js</p>
        <hr class="bg-info mb-5">
        <span><img alt="Vue logo" src="../assets/logo.png" width="40"></span>
    </div>

    <div class="container">
        <router-link to="/add">
            <button class="btn btn-success btn-block my-5">Add a new task</button>
        </router-link>

        <h2 class="display-2 text-center">Task list</h2>
        <hr class="bg-info">

        <form @submit.prevent="searchBar(searchinput)">
            <input type="text" placeholder="Search..." class="form-control border-info mt-5" v-model="searchinput" id="searchbar" v-on:keyup="searchBar(searchinput)">
        </form>

        <!-- SPINNER -->
        <div class="container text-center" v-if="loading">
            <h4 class="display-4 text-warning">Loading data...
                <dot-loader :loading="loading"></dot-loader>
            </h4>
        </div>

        <!-- SPINNER -->

        <ol id="taskList" v-if="!loading">
            <li class="d-flex justify-content-between" v-for="item of filteredArray" :key="item.id"> Task name: {{item.item}} - ID: {{item.id}}

                <router-link :to="{name: 'Edit', params: {id: item.id}}">
                    <button class="btn btn-warning btn-lg">Edit task</button>
                </router-link>
                <button class="btn btn-danger btn-lg" @click="removeTask(item.id)">Remove</button>

            </li>
        </ol>
    </div>
</div>
</template>

<script>
import DotLoader from 'vue-spinner/src/DotLoader.vue'

import {
    mapActions,
    mapState,
    mapGetters
} from 'vuex'

export default {
    name: "Inicio",
    data() {
        return {
            searchinput: ''
        }
    },
    created() {
        this.getTasks()
    },
    methods: {
        ...mapActions(['getTasks', 'removeTask', 'searchBar'])
    },
    computed: {
        ...mapState(['tasks', 'loading']),
        ...mapGetters(['filteredArray'])
    },
    components: {
        DotLoader
    }
}
</script>

<style scoped>
#taskList {
    padding-top: 50px;
}

.btn-success {
    text-transform: uppercase;
    font-size: 20px;
    margin-left: 10px;
    border-color: transparent !important;
    border-radius: 90px;

}

.btn-warning {
    padding: 10px;
    border-radius: 15px;
    float: right;
}

.btn-danger {
    padding: 10px;
    align-items: right;
    border-radius: 15px;
    margin-right: 20px;
    float: right;

}

li {
    font-size: 20px;
    padding-bottom: 20px;
    padding-top: 20px;
    padding-left: 15px;
    border: 1px solid rgba(88, 195, 214, 0.527);
    border-radius: 10px;
    margin-top: 5px
}

#searchbar {
    background-color: transparent;
    color: aqua;
    font-size: 32px;
}
</style>
