<template>
<div class="container pt-5">

    <div>
        <h1 class="display-3">Add task

            <a href="/" type="button" class="btn btn-outline-warning" role="button" aria-pressed="true">Go back</a>

        </h1>
        <hr class="bg-info">

        <form @submit.prevent="addTask(item)">
            <div class="form-group pt-5">
                <input class="form-control" type="text" v-model="$v.item.$model" placeholder="Write your task">
                <small class="text-warning" v-if="!$v.item.required">Required field</small>
                <small class="text-warning d-block" v-if="!$v.item.minLength">This must be 5 characters at least</small>
                <p> {{ $v.item }} </p>

                <button class="btn btn-info btn-lg btn-block mt-5" type='submit' :disabled="$v.$invalid || loading">Add</button>

            </div>
        </form>

    </div>
</div>
</template>

<script>
import {
    mapActions,
    mapState
} from 'vuex'

import {
    required,
    minLength
} from 'vuelidate/lib/validators'

export default {
    name: 'Add',
    data() {
        return {
            item: ''
        }
    },
    methods: {
        ...mapActions(['addTask'])
    },
    validations: {
        item: {
            required,
            minLength: minLength(6)
        }
    },
    computed: {
        ...mapState(['loading'])
    }
}
</script>

