<template>
<div class="container pt-5">

    <div>
        <h1 class="display-3">Edit task

            <a href="/" type="button" class="btn btn-outline-warning" role="button" aria-pressed="true">Go back</a>

        </h1>

    </div>
    <hr class="bg-info">

    <div class="row pt-5">
        <div class="col">
            <h3 class="text-white">Task Name:</h3>
            <p class="lead">{{task.item}}</p>
        </div>
        <div class="col">
            <h3 class="text-white">Task ID:</h3>
            <p class="lead">{{task.id}}</p>
        </div>
    </div>

    <form @submit.prevent="editTask(task)">
        <div class="form-group">
            <input class="form-control" type="text" v-model="$v.task.item.$model">
            <small class="text-warning" v-if="!$v.task.item.required">Field required</small>

            <button class="btn btn-success btn-lg btn-block mt-5" type='submit' id="saveBtn" :disabled="$v.task.$invalid">Save changes</button>

        </div>
    </form>

</div>
</template>

<script>
import {
    required,
    minLength
} from 'vuelidate/lib/validators'

import {
    mapActions,
    mapState
} from 'vuex'

export default {
    name: "Edit",
    data() {
        return {
            id: this.$route.params.id
        }
    },
    created() {
        this.getTask(this.id)
    },
    methods: {
        ...mapActions(['getTask', 'editTask'])
    },
    computed: {
        ...mapState(['task'])
    },
    validations: {
        task: {
            item: {
                required
            }
        }
    }
}
</script>

